@keyframes move-to-right {
  0% {
    z-index: 1;
    transform: translateX(0%);
  }

  100% {
    z-index: 1;
    transform: translateX(100%);
  }
}

.move-to-right {
  animation-name: move-to-right;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

@keyframes move-to-left {
  0% {
    z-index: 1;
    transform: translateX(0%);
  }

  100% {
    z-index: 1;
    transform: translateX(-100%);
  }
}

.move-to-left {
  animation-name: move-to-left;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

@keyframes bring-from-left {
  0% {
    z-index: 1;
    transform: translateX(-100%);
  }

  100% {
    z-index: 1;
    transform: translateX(0%);
  }
}

.bring-from-left {
  animation-name: bring-from-left;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

@keyframes bring-from-right {
  0% {
    z-index: 1;
    transform: translateX(100%);
  }

  100% {
    z-index: 1;
    transform: translateX(0%);
  }
}

.bring-from-right {
  animation-name: bring-from-right;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.spin-animation {
  animation-name: spin;
  animation-duration: 120s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

@keyframes move-to-right-for-van {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(30rem);
  }
}

@keyframes move-to-right-for-van-short {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(10rem);
  }
}

@keyframes hero-text-animation {
  0% {
    text-shadow: 2px 2px 10px gray;
  }

  50% {
    text-shadow: 4px 4px 20px #fff;
  }

  100% {
    text-shadow: 2px 2px 10px gray;
  }
}

@keyframes circle-in-top-right {
  from {
    -webkit-clip-path: circle(0% at 100% 0);
    clip-path: circle(0% at 100% 0);
  }

  to {
    -webkit-clip-path: circle(125%);
    clip-path: circle(125%);
  }
}

@keyframes circle-out-top-right {
  0% {
    visibility: visible;
    opacity: 1;
    -webkit-clip-path: circle(125%);
    clip-path: circle(125%);
  }

  99% {
    visibility: visible;
    opacity: 1;
    -webkit-clip-path: circle(0% at 100% 0);
    clip-path: circle(0% at 100% 0);
  }

  100% {
    visibility: hidden;
    opacity: 0;
  }
}

